@font-face {
  font-family: "Oswald Regular";
  src: url("../fonts/oswaldregular.woff") format("woff");
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Oswald Regular", sans-serif;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.page-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-height: 65px;
  border-bottom: 1px solid #ededed;
}
.page-header__logo {
  width: 191px;
  height: 39px;
  margin-bottom: 4px;
  margin-left: 20px;
}
.main-nav__toggle {
  margin-right: 19px;
  border: none;
  background-color: white;
}
.main-nav__toggle img {
  position: relative;
  margin: 0 auto;
  width: 25px;
  height: 19px;
}
.main-nav__wrapper {
  z-index: 1;
  position: absolute;
  left: 0px;
  width: 100%;
}
.site-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  padding-inline-start: 0px;
}
.site-list__item {
  border-top: 1px solid #ededed;
  width: 100%;
  min-height: 65px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.site-list__item a {
  color: black;
  font-size: 20px;
  text-align: center;
}
.site-list__item__item:nth-child(3) {
  border-bottom: 1px solid #ededed;
}
@media (min-width: 768px) {
  .page-header {
    max-height: 59px;
    margin-top: 24px;
    border: none;
  }
  .page-header__logo {
    margin-left: 29px;
    width: 174px;
    height: 59px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__wrapper {
    z-index: 0;
    position: inherit;
  }
  .site-list {
    flex-direction: row;
  }
  .site-list__item:nth-child(1) {
    margin-right: 36px;
  }
  .site-list__item:nth-child(2) {
    margin-right: 38px;
  }
  .site-list__item:nth-child(3) {
    margin-right: 31px;
  }
  .site-list__item {
    border: none;
    width: auto;
  }
  .site-list__item a {
    color: #222222;
    font-size: 18px;
    text-align: left;
    line-height: 25px;
  }
  .site-list__item--active {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1300px) {
  .page-header {
    min-height: 113px;
    width: 100%;
    align-items: flex-end;
  }
  .page-header__logo {
    width: 203px;
    height: 60px;
    margin-left: 109px;
  }
  .page-header--desktop-type {
    position: absolute;
    left: 0;
    top: 0;
  }
  .site-list {
    justify-content: space-between;
    background-color: transparent;
    margin-right: 110px;
  }
  .site-list__item {
    margin: 0;
  }
  .site-list--desktop-type .site-list__item--active {
    border-bottom: 2px solid white;
  }
  .site-list--desktop-type .site-list__item a {
    color: white;
  }
